import React from "react";
import { TiHome } from "react-icons/ti";
import styled from "styled-components";
import { useAuth } from "../Context/Auth";
import { FaUserCog } from "react-icons/fa";
import { NavLink, useNavigate, useParams } from "react-router-dom";
import { MdClose } from "react-icons/md";
import { BiAddToQueue } from "react-icons/bi";
import { useCookies } from "react-cookie";

const SideNavigationBar = () => {
  const auth = useAuth();
  const param = useParams();
  const navigate = useNavigate();
  // eslint-disable-next-line no-unused-vars
  const [cookie, setCookie, removeCookie] = useCookies(["log"]);

  const handleLogout = () => {
    removeCookie("log", { path: "/" });
    auth.logout();
    navigate("/", { replace: true });
  };

  return (
    <Nav>
      <Container>
        <Brand>
          <Image
            src={require("../components/images/BOU logo.png")}
            alt="logo"
          />
          <BrandPanel>
            <BrandText>বাংলাদেশ উন্মুক্ত বিশ্ববিদ্যালয়</BrandText>
            <BrandText>Bangladesh Open University</BrandText>
          </BrandPanel>
        </Brand>
        <BarIcon>
          <MdClose />
        </BarIcon>
      </Container>
      <UL>
        <Link to={`/t/${param.tid}/dashboard`}>
          <LI>
            <Icon>
              <TiHome />
            </Icon>
            Dashboard
          </LI>
        </Link>
        <Div>
          {auth.isDashboard && (
            <Link to={`/t/${param.tid}/class/create`}>
              <LI>
                <Icon>
                  <BiAddToQueue />
                </Icon>
                Create
              </LI>
            </Link>
          )}
        </Div>
        <BottomTools>
          <LI>
            <Icon>
              <FaUserCog />
            </Icon>
            Profile
          </LI>
          <LI style={{ paddingLeft: "1.5rem" }} onClick={handleLogout}>
            <Icon
              style={{
                width: "30px",
                height: "30px",
                overflow: "hidden",
                justifyContent: "center",
                marginRight: "10px",
              }}
              //   onClick={handleLogout}
            >
              <Image
                // onClick={handleLogout}
                style={{ width: "30px", height: "30px", padding: "0" }}
                src={auth.user.image || require("../components/images/man.png")}
                alt="user"
              />
            </Icon>
            Logout
          </LI>
        </BottomTools>
      </UL>
    </Nav>
  );
};

export default SideNavigationBar;

const Nav = styled.nav`
  display: flex;
  flex-direction: column;
  margin-top: 1rem;
  padding: 0.5rem 0.1rem;
`;

const Container = styled.div`
  display: flex;
  flex-wrap: nowrap;
`;
const BarIcon = styled.div`
  width: 40px;
  height: 40px;
  border: 2px solid #fff;
  border-radius: 3px;
  color: #fff;
  display: none;
  justify-content: center;
  align-items: center;
  font-size: 1.3rem;
  cursor: pointer;
  position: relative;
  margin: 0 10px 0 auto;
  &:hover {
    background: red;
    border-color: red;
  }
  @media (max-width: 820px) {
    display: flex;
  }
`;

const UL = styled.ul`
  display: flex;
  flex-direction: column;
  margin: 0;
  padding-left: 0;
  height: calc(100vh - 100px);
`;
const LI = styled.li`
  white-space: nowrap;
  font-weight: 600;
  padding: 0.5rem 1rem;
  padding-left: 2rem;
  display: flex;
  align-items: center;
  cursor: pointer;
  &:hover {
    background: #fff;
    color: #333;
  }
`;
const Icon = styled.div`
  width: 30px;
  height: 20px;
  display: flex;
  align-items: center;
`;

const Image = styled.img`
  width: 40px;
  height: 40px;
  padding: 0.2rem 0.3rem;
  border-radius: 5px;
  background: rgba(241, 242, 246, 1);
`;
const Brand = styled.div`
  display: flex;
  flex-wrap: nowrap;
  white-space: nowrap;
  gap: 0 0.4rem;
  align-items: center;
  margin-bottom: 2rem;
`;
const BrandPanel = styled.div`
  display: flex;
  flex-direction: column;
`;

const BrandText = styled.h1`
  font-size: 0.9rem;
  font-weight: 600;
  margin-bottom: 0;
`;

const Link = styled(NavLink)`
  text-decoration: none;
  color: #fff;
`;

const BottomTools = styled.div`
  display: none;
  @media (max-width: 820px) {
    display: block;
    margin: auto 0 1rem 0;
  }
`;

const Div = styled.div`
  display: none;
  @media (max-width: 820px) {
    display: block;
  }
`;
